import React from "react"
import Head from "next/head"
import { style, media } from "typestyle"
import fetch from "isomorphic-unfetch"

import HStack from "../../components/HStack"

import Header from "../../components/Header"
import Footer from "../../components/Footer"

import ResponsiveContainer from "../../components/ResponsiveContainer"
import Spacer from "../../components/Spacer"
import MarkdownView from "../../components/MarkdownView"

import { EYRIE_URL } from "../../utils"
import md5 from "blueimp-md5"

declare var __NEXT_DATA__: any
declare var requestIdleCallback: any
declare var Fingerprint2: any

const s = {
    titleContainer: style({
        width: "100%",
        marginBottom: "30px"
    }),
    title: style({
        margin: "0 0 10px 0",
        fontSize: "3rem"
    }),
    meta: style({
        width: "100%",
        borderBottom: "1px dashed rgba(0, 0, 0, 0.4)"
    }),
    category: style({
        fontWeight: "bold",
        lineHeight: "2rem"
    }),
    date: style({
        marginLeft: "10px",
        lineHeight: "2rem"
    }),
    passwordField: style({
        width: "100%",
        height: "3rem",
        border: "0.5px solid rgba(0, 0, 0, 0.2)",
        fontSize: "1.1rem",
        padding: "0 0.7rem",
        boxSizing: "border-box",
        transition: "box-shadow linear 300ms",
        textAlign: "center",
        "$nest": {
            "&:focus": {
                outlineWidth: "0",
                boxShadow: "0 5px 10px rgba(0, 0, 0, 0.1)"
            }
        }
    }),
    button: style({
        width: "100%",
        maxWidth: "400px",
        fontSize: "0.9rem",
        border: "none",
        boxShadow: "0 5px 12px rgb(134, 186, 251)",
        padding: "0.6rem 0.8rem",
        backgroundColor: "#74b0fb",
        color: "#FFFFFF",
        fontWeight: "bold",
        transition: "box-shadow linear 100ms",
        borderRadius: "3px",
        cursor: "pointer",
        marginTop: "2rem",
        "$nest": {
            "&:focus": {
                outlineWidth: "0"
            },
            "&:active": {
                boxShadow: "0 2px 6px rgb(134, 186, 251)",
                backgroundColor: "#86bafb",
                color: "#FEFEFE",
                "-webkit-tap-highlight-color": "transparent"
            }
        }
    }),
    bodyLoading: style({
        fontFamily: "Impact, sans-serif",
        fontSize: "1.8rem",
        color: "#5696E7"
    }),
    watermark: style({
        position: "fixed",
        left: "-20vw",
        top: "0",
        width: "100vw",
        height: "113px",
        fontSize: "1.25rem",
        color: "rgba(0, 0, 0, 0.2)",
        textAlign: "center",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        fontWeight: 700,
        zIndex: 10
    }, media({ minWidth: 320, maxWidth: 480 }, {
        left: "0",
        height: "62px",
    })),
    ert: style({
        marginLeft: "10px",
        lineHeight: "2rem",
        opacity: 0.4
    })
}

interface IClassifiedArticleItemProps {
    date: string
}

interface IClassifiedArticleItemState {
    article: any
    authored: boolean
    loading: boolean
}

class ClassifiedArticleItem extends React.Component<IClassifiedArticleItemProps, IClassifiedArticleItemState> {
    static async getInitialProps({ req }) {
        if (req) {
            const dayjs = require("dayjs")
            const date = dayjs().format("MMM D, YYYY")

            return { date }
        }
        return __NEXT_DATA__.props.pageProps
    }

    constructor(props) {
        super(props)
        this.state = {
            article: null,
            authored: false,
            loading: false
        }
    }

    onProceedButtonClick = async () => {
        // Debounce
        if (this.state.loading) return

        let url = new URL(window.location.href)
        let id = url.searchParams.get("id")

        let password = (document.getElementById("password") as HTMLInputElement).value

        if (!!password && !!id) {
            let payload

            if (requestIdleCallback) {
                payload = await (() => (new Promise(resolve => requestIdleCallback(function () {
                    Fingerprint2.get((components) => {
                        resolve(components)
                    })
                }))))()
            } else {
                payload = await (() => (new Promise(resolve => requestIdleCallback(function () {
                    Fingerprint2.get((components) => {
                        resolve(components)
                    })
                }))))()
            }

            payload = (payload as Array<any>).reduce((obj, v) => (
                v.key !== "canvas"
                    ? Object.assign({}, obj, { [v.key]: v.value })
                    : obj
            ), {})

            let data = {
                id,
                password,
                fingerprint: md5(JSON.stringify(payload)),
                payload
            }

            console.log(data)

            // Start network job
            this.setState({ loading: true })

            fetch(`${EYRIE_URL}/api/classified`, {
                method: "POST", // *GET, POST, PUT, DELETE, etc.
                mode: "cors", // no-cors, *cors, same-origin
                cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
                headers: {
                    "Content-Type": "application/json"
                },
                // credentials: "include",
                redirect: "follow", // manual, *follow, error
                referrer: "no-referrer", // no-referrer, *client
                body: JSON.stringify(data) // body data type must match "Content-Type" header
            }).then(response => {
                if (response.ok) {
                    return response.json()
                } else {
                    return Promise.reject(response)
                }
            }).then(data => {
                this.setState({ article: data, authored: true, loading: false })

                setTimeout(() => {
                    window.scrollTo(0, 0)
                }, 10)

                setTimeout(() => {
                    (document.getElementById("embedded-music") as HTMLIFrameElement).src = data.meta.music
                }, 500)
            }).catch(err => {
                alert("Authorization failed!")
                this.setState({ authored: false, loading: false })
            })
        }
    }

    render() {
        let { authored, article, loading } = this.state

        let date = this.props.date
        let title = "ðŸ”’ Require Authentication"

        let meta = !authored
            ? (<Spacer />)
            : (<HStack className={s.meta}>
                <div className={s.category}>
                    {article.meta.category}
                </div>
                <div className={s.date}>
                    {article.meta.date}
                </div>
                <div className={s.ert}>
                    {Math.ceil(article.body.length / 400)}min
                </div>
            </HStack>)

        let body = !authored
            ? (<div>
                <input id="password" className={s.passwordField} type="password" />
                <p>You are accessing a classified article, password authentication is required. The password should already been provided from Yiqin to you, please use it.</p>
                <p><b>Note:</b> Please <b>DO NOT</b> <u>redistribute</u> or <u>share any information</u> from classified articles, including <i>copy-paste</i>, <i>screenshot</i>, <i>print</i>, etc. To protect the classified information, your usage of <a href="yiqinzhao.me">yiqinzhao.me</a> on classified articles <b>will be tracked</b>. By clicking the <i>Proceed</i> button, you have acknowledged and agree the above regulations.</p>
                <HStack align="flex-end">
                    <button className={s.button} onClick={this.onProceedButtonClick}>Proceed</button>
                </HStack>
            </div>)
            : <MarkdownView content={article.body} />

        body = loading
            ? (<div className={s.bodyLoading}>Loading...</div>)
            : body

        let musicSection = article && article.meta.music ? (
            <ResponsiveContainer>
                <Spacer />
                <iframe id="embedded-music" frameBorder="0" height="86" width="330" src="data:text/html,<div style='font-size: 40px; text-align: center; width: 100%; line-height: 66px; font-family: Impact, sans-serif; color: rgba(0, 0, 0, 0.1)'>Loading Music</div>"></iframe>
                <Spacer />
            </ResponsiveContainer>
        ) : <></>

        let watermark = authored
            ? <div className={s.watermark}>Accessor: {article.accessor}</div>
            : <></>

        return (
            <div className="classified-container main-container">
                <Head>
                    <title>ðŸ”’ {authored ? article.meta.title : "Classified"} | YiqinZhao</title>
                    <script src="//cdnjs.cloudflare.com/ajax/libs/fingerprintjs2/2.1.0/fingerprint2.min.js"></script>
                </Head>

                <Header />

                {watermark}

                <ResponsiveContainer>
                    <Spacer />
                    <div className={s.titleContainer}>
                        <h1 className={s.title}>
                            {authored ? article.meta.title : title}
                        </h1>
                        {meta}
                    </div>
                    <Spacer />
                </ResponsiveContainer>

                {musicSection}

                <ResponsiveContainer>
                    <Spacer />
                    {body}
                    <Spacer />
                </ResponsiveContainer>

                <Footer date={date} />
            </div>
        )
    }
}

export default ClassifiedArticleItem
